import Link from 'next/link'

function Footer() {
  return (
    <footer style={navStyle}>

      <div>
        <h1 style={boldText}>Copyright</h1>
        <p> &copy; 2019 </p>
        
        <Link href='/'>
            <a style={linkStyle}>Home</a>
        </Link>
        <Link href='/about'>
            <a style={linkStyle}>About</a>
        </Link>
      </div>

      <div>        
        </div>

    </footer>
  )
}

const linkStyle = {
    textDecoration: 'none',
    weight: 'bold',
    color: '#ffffff',
    marginRight: '15px',
  }

  const boldText = {
    textDecoration: 'none',
    weight: 'bold',
    color: '#fff',
    textAlign: 'left',
    marginRight: '10px'
}
  
  const navStyle = {
    background: '#05052b',
    position: 'fixed',
    bottom: '0',
    width: '100%',
    height: '80px',
    lineHeight: '80px',
    padding: '0 20px',
    textAlign: 'right',
    padding: '10px',
    color: '#ffffff',

  }

export default Footer;